import Card from "@/components/Card";

export default function DashboardPage() {
  return (
    <div>
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-2xl font-semibold">Dashboard</h2>
        <div className="text-sm text-gray-500">Today: {new Date().toLocaleDateString()}</div>
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-5 gap-4">
        <Card title="Total Employees" value="120" />
        <Card title="Currently Working" value="87" />
        <Card title="On Break" value="12" />
        <Card title="Time Off" value="8" />
        <Card title="Pending Biometrics" value="3" />
      </div>

      <div className="mt-6 grid grid-cols-1 lg:grid-cols-3 gap-6">
        <div className="col-span-2 bg-white rounded-xl shadow p-6 h-80">
          <h3 className="text-lg font-medium mb-4">Attendance Summary</h3>
          <div className="w-full h-56 bg-gray-50 rounded border-dashed border-2 border-gray-200 flex items-center justify-center text-gray-300">
            Charts Placeholder
          </div>
        </div>

        <div className="bg-white rounded-xl shadow p-6 h-80">
          <h3 className="text-lg font-medium mb-4">Recent Activity</h3>
          <ul className="space-y-3 text-sm text-gray-600">
            <li>Ramesh applied for leave</li>
            <li>Payroll processed for Oct</li>
            <li>2 employees marked present late</li>
          </ul>
        </div>
      </div>
    </div>
  );
}
